body{
    background-color: rgb(37, 22, 3);
    margin:0;
    overflow: hidden;

    div{
        width: 50vw;
        height: 100vh;
        background-color: rgb(216, 127, 19);
        div{
            position: absolute;
            width: 500px;
            height: 500px;
            background-color: rgb(22, 228, 53);
            border: 1px solid blue;
        }
        p{
            position: relative;
            color: white;
            top: 50%;
            left: 42%;
            z-index: 2;
        }

    }
}
